import { SEPARATOR } from 'src/utils/translate'
import axios, { AxiosProxyConfig } from 'axios'
import { Translator } from './translator'
import CryptoJS from 'crypto-js'

export interface BaiduTranslatorOption {
    appId: string
    appKey: string
    proxy?: AxiosProxyConfig
    /** ç¿»è¯‘apiæ‰§è¡Œé—´éš”ï¼Œé»˜è®¤ä¸º1000 */
    interval?: number
}

export class BaiduTranslator extends Translator {
    /** ç™¾åº¦çš„è¯­è¨€ç±»å‹æ˜ å°„ä¸æ ‡å‡†ï¼Œéœ€è¦æ‰‹åŠ¨æ§åˆ¶ */
    private readonly Baidu_TRANSLATE_KEY_CONVERT_MAP: Record<string, string> = {
        'zh-cn': 'zh',
        ja: 'jp',
        ko: 'kor'
    }

    private getTranslateKey(key: string) {
        return this.Baidu_TRANSLATE_KEY_CONVERT_MAP[key] || key
    }

    constructor(option: BaiduTranslatorOption) {
        super({
            name: 'ç™¾åº¦ç¿»è¯‘',
            fetchMethod: async (text, fromKey, toKey) => {
                let salt = new Date().getTime()

                const data = {
                    q: text,
                    appid: option.appId,
                    from: this.getTranslateKey(fromKey),
                    to: this.getTranslateKey(toKey),
                    salt,
                    sign: CryptoJS.MD5(option.appId + text + salt + option.appKey).toString()
                }
                const response = await axios.post(
                    'https://fanyi-api.baidu.com/api/trans/vip/translate',
                    data,
                    {
                        headers: {
                            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
                        },
                        proxy: option.proxy
                    }
                )

                const translatedTexts = response.data?.trans_result
                    .map((item: any) => item.dst)
                    .filter((_item: string, index: number) => index % 2 === 0)
                    .join(SEPARATOR)

                // è¯·æ±‚æˆåŠŸï¼Œè¿”å›å“åº”æ•°æ®
                return translatedTexts || ''
            },
            onError: (error, cb) => {
                cb(error)
                console.error(
                    'è¯·å‰å¾€ç™¾åº¦ç¿»è¯‘å®˜æ–¹ç”³è¯·ç¿»è¯‘keyï¼Œæ¯ä¸ªæœˆéƒ½æœ‰å…è´¹é¢åº¦ï¼Œå¹¶è¯·æ£€æŸ¥é¢åº¦æ˜¯å¦å……è¶³ã€‚'
                )
            },
            interval: option.interval ?? 1000
        })
    }
}
